.jet-clipboard {
	display: inline-flex;
	flex-direction: column;
	align-items: stretch;
	cursor: pointer;

	&.copied {
		cursor: copy;

		.jet-clipboard-icon-copied {
			opacity: 1;
			visibility: visible;
		}

		.jet-clipboard-icon-copy {
			opacity: 0;
			visibility: hidden;
		}
	}
}

.jet-clipboard-icon {
	position: relative;
}

.jet-clipboard-icon-copy {
	display: flex;
	transition: 0.5s;
	align-items: center;
	border: 1px solid #000;
	padding: 6px 10px;
	border-radius: 8px;

	&:hover {
		background: #000;
		color: #f2f2f2;

		svg path {
			fill: #f2f2f2;
		}
	}
}

.jet-clipboard-icon-copied {
	display: flex;
	position: absolute;
	align-items: center;
top: 0;
	left: 0;
	bottom: 0;
	transition: 0.5s;
	z-index: 100;
}